* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Futura PT';
}

.section {
    background-color: #000000;
}

.container {
    padding: 0 16px;
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
}

img {
    max-width: 100%;
    height: auto;
    width: 100%;
}

body {
    color: white;
}

.header {
    background-color: #000000;
}

.footer {
    background-color: #000000;
}

.info__container {
    padding-bottom: 10px;
    max-width: 1400px;
    width: 100%;
}

.info__nav {
    display: flex;
    align-items: center;
    justify-content: center;
}

.info-rhomb {
    width: 61px;
    height: 61px;
    background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
    transform: rotate(45deg);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.rhomb-text {
    transform: rotate(-45deg);
    color: rgba(255, 255, 255, 1);
    font-size: 13px;
    text-align: center;
}

.info__nav-list {
    display: flex;
    justify-content: space-between;
    list-style-type: none;
    max-width: 650px;
    width: 100%;
    text-transform: uppercase;
    font-size: 17px;
    font-weight: 400;
    line-height: 22px;
    margin-left: 147px;
    margin-right: 100px;
}

.info__nav-icons {
    display: flex;
    flex-shrink: 0;
    gap: 20px;
    margin-right: 102px;
}


.info__nav-login {
    display: flex;
    align-items: center;
    gap: 10px;
    text-decoration: none;
}

.info__nav-logintext {
    color: rgba(255, 51, 51, 1);
}

.intro__payblock {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(/src/img/background_intro.png);
    background-repeat: no-repeat;
    padding-top: 104px;
}

.intro__pay {
    display: flex;
    align-items: center;
    gap: 50px;
}

.intro__pay-text {
    font-family: "Lato";
    font-size: 110px;
    font-weight: 500;

}

.intro__pay-rhomb {
    width: 100px;
    height: 100px;
    background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
    transform: rotate(45deg);
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro__pay-rhombtext {
    transform: rotate(-45deg);
    color: rgba(255, 255, 255, 1);
    font-size: 22px;
}

.intro__text {
    font-family: "Lato";
    font-size: 110px;
    font-weight: 500;
    /* margin-left: 100px; */
}

.intro__block {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin-top: 50px;
    padding-bottom: 210px;
}

.intro__block-text {
    font-size: 24px;
    font-weight: 400;
    width: 304px;
    text-align: justify;
    line-height: 31.2px;
}

.intro__block-button {
    width: 244px;
    height: 70px;
    background-color: #FF3333;
    font-size: 24px;
    color: white;
}



.games {
    background-color: white;

}

.games_container {
    max-width: 1920px;
}

.games_list {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    gap: 100px;
}

.games__list-img {
    width: 80px;
    height: 42px;
}


.goals {
    background-image: radial-gradient(50% 50% at 50% 50%, rgba(255, 51, 51, 0.5) 0%, rgba(255, 51, 51, 0) 100%);
    background-size: 100% 712px;
    background-repeat: no-repeat;
}

.goals__info {
    margin-top: 25px;
}

.goals__blocks {
    display: flex;
    justify-content: center;
    gap: 30px;
}


.goals__blocks-article {
    width: 319px;
    height: 282px;
    text-align: center;
    padding-top: 30px;
    font-size: 22px;
    line-height: 28px;
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%), linear-gradient(0deg, #000000, #000000);
    border: 1px solid;
    border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
    box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset;
}

.goals__article-text {
    max-width: 291px;
}

.goals__rhomb {
    width: 61px;
    height: 61px;
    background: linear-gradient(176.47deg, #FF3333 -36.48%, rgba(255, 51, 51, 0) 95.83%);
    transform: rotate(45deg);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.goals__rhombtext {
    transform: rotate(-45deg);
    font-size: 40px;
}

.goals__title {
    font-family: "Lato", sans-serif;
    font-size: 100px;
    font-weight: 500;
    line-height: 100px;
    text-align: center;
    padding-top: 100px;
}

.goals__text-intro {
    font-size: 22px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 60px;
}

.goals__footer {
    padding: 300px 0;
    background-image: url(/src/img/stacking/Ellipse.png);
    background-repeat: no-repeat;
    background-position: 42% 50%;
}

.goals__footer-text {
    margin: 0 auto;
    font-size: 50px;
    font-weight: 400;
    line-height: 65px;
    text-align: center;
    width: 933px;
}


