    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: red;
} */

.diagram {
    background-color: white;
}

.diagram__container {
    color: #000000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.diagram__text-vip {
    font-size: 100px;
    font-weight: 400;
    line-height: 130px;
}

.diagram__text-usdt {
    font-size: 90px;
    font-weight: 400;
    line-height: 117px;
}

.diagram__block {
    width: 435px;
    margin: 100px 0;
}

.diagram__block-text {
    margin-top: 15px;
    font-size: 24px;
    font-weight: 400;
}


.stacking__container {
    padding-bottom: 100px;
}

.stacking__info {
    margin: 30px auto;
    max-width: 1200px;
    width: 100%;
    background-image: url('/src/img/stacking/background_stacking.png'), url('/src/img/stacking/Ellipse.png');
    background-repeat: no-repeat;
    background-position: center;
    padding: 70px 0;
}

.stacking__info-title {
    font-family: "Lato", sans-serif;
    font-size: 110px;
    font-weight: 500;
    line-height: 143px;
    text-align: center;
}

.stacking__info-intro {
    font-size: 24px;
    font-weight: 400;
    line-height: 31.2px;
    width: 250px;
}

.stacking__info-footer {
    font-size: 24px;
    font-weight: 400;
    line-height: 31.2px;
    width: 320px;
    margin-left: auto;
}

.stacking__blocks {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.stacking__blocks-article {
    width: 319px;
    height: 206px;
    text-align: center;
    padding: 19px 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 20px;
    font-weight: 400;
    background: linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%);
    border: 1px solid;
    border-radius: 8px;
    border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
    box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset;
    box-shadow: -5px -5px 20px 0px rgba(255, 255, 255, 0.3);

}

.stacking-blocks-intro {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
}

.packets {
    background: white;
}

.packets__container {
    color: #000000;
    padding-bottom: 100px;
}

.packets__intro {
    display: flex;
    justify-content: center;
    gap: 248px;
    margin-bottom: 78px;
}

.packets__intro-title {
    font-family: "Lato", sans-serif;
    font-size: 110px;
    font-weight: 500;
    margin: 100px 0;
}

.packets__intro-text {
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;
    text-align: left;
    width: 509px;
    padding-top: 172px;
}

.packets__blocks {
    display: flex;
    justify-content: center;
    gap: 40px;
}

.packets__blocks-article {
    width: 250px;
    text-align: center;
    border: 2px solid rgba(255, 51, 51, 1);
    border-radius: 8px;
    position: relative;
}

.packets__article-title {
    padding-top: 63px;
    font-size: 50px;
    font-weight: 600;
    line-height: 65px;
    color: rgba(255, 51, 51, 1);
}

.packets__article-percent {
    padding: 10px 0;
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;
}

.packets__article-time {
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;
}

.packets__article-available {
    padding: 50px 0 30px;
    font-size: 22px;
    font-weight: 400;
    line-height: 28px;
}

.packets__article-image {
    position: absolute;
    top: -30px;
    left: 50%;
    transform: translate(-50%);
}

.packets__image-style {
    width: 60px;
    height: 60px;
    background: rgba(255, 51, 51, 1);
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(45deg);
}

.packets__image-text {
    transform: rotate(-45deg);
    color: white;
    font-size: 24px;
}

.referal {
    background: #000000;
    padding-bottom: 100px;
}

.referal__intro {
    margin: 0 auto;
    padding: 29px 0 31px;
    background-image: url(/src/img/stacking/background_stacking.png), url(/src/img/stacking/Ellipse.png);
    background-repeat: no-repeat;
    background-position: center;
    padding: 171px 0 69px;
    display: flex;
    flex-direction: column;
    margin-bottom: 31px;
}

.referal__intro-title {
    font-family: Lato;
    font-size: 110px;
    font-weight: 500;
    line-height: 143px;
    text-align: center;
    margin-bottom: 40px;
}

.referal__intro-text {
    font-size: 24px;
    font-weight: 400;
    line-height: 31.2px;
    max-width: 359px;
    width: 100%;
    align-self: flex-end;
    margin-right: -113px;
}

.referal__footer {
    display: flex;
    gap: 30px;
}

.referal-item {
    max-width: 554px;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    border: 1px solid;
    border-image-source: linear-gradient(131.37deg, rgba(47, 46, 46, 0.5) 0%, rgba(255, 255, 255, 0) 67.7%);
    box-shadow: 30px 30px 36px 0px rgba(21, 21, 21, 0.5) inset, -5px -5px 20px 0px rgba(255, 255, 255, 0.3);
    border-radius: 8px;
    background:
        linear-gradient(130.66deg, rgba(255, 255, 255, 0.3) -19.06%, rgba(255, 255, 255, 0) 31.37%);




}

.referal-item__title {
    font-size: 36px;
    font-weight: 600;
    line-height: 43px;
    margin-bottom: 30px;
}

.referal-item__text {
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;
    margin-bottom: 80px;
}

.referal-item__percent {
    display: flex;
    gap: 66px;
}

.referal-item__percent-item {
    width: 148px;
    flex-shrink: 0;
}

.referal-item__percent-title {
    font-size: 26px;
    font-weight: 400;
    line-height: 34px;
}

.referal-item__percent-text {
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 50px;
}

.referal-item__example {
    display: flex;
    gap: 30px;
}

.referal-item__example-figure {
    width: 60px;
    height: 60px;
    background-color: rgba(255, 51, 51, 1);
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(45deg);
}

.referal-item__example-text {
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    width: 327px;
    margin-bottom: 66px;
}

.referal-item__footer {
    margin-left: auto;
}